@import "../../css/shared.scss";

.p-share{
  position: relative;

  .bg{
    width: 100%;
    display: block;
  }

  .qrcode{
    width: 100%;
    position: absolute;
    top: px(1240);
    left: 0;

    img{
      display: block;
      width: px(320);
      height: px(320);
      margin: 0 auto;
      border: px(20) solid #fff;
      background: #fff;
    }
  }
}
